.loading::after {
  -webkit-animation: loading 1s infinite;
          animation: loading 1s infinite;
  content: "";
}
@-webkit-keyframes loading {
  25% {
    content: ".";
  }
  50% {
    content: "..";
  }
  75% {
    content: "...";
  }
  100%  {
    content: "";
  }
}
@keyframes loading {
  25% {
    content: ".";
  }
  50% {
    content: "..";
  }
  75% {
    content: "...";
  }
  100%  {
    content: "";
  }
}
.message.animation {
  width: 140px;
  text-align: left;
}



